.sticky-header {
  position:absolute;
  height: 79px;
  top: 0;
  width: 100%;
  left: 0;
  background-color: var(--orange);
  color: var(--white);
}
.sticky-header > div {
  height: 79px;
  margin: 0 17px 0 11px;
}
#placeholder {
  /*border: 2px solid yellow;*/
  width: 100%;
  height: 79px;
}
#title {
  font-family: 'Cerebri Bold';
  font-size: 22px;
}
#bttn-arrow, #bttn-power {
  width: 28px;
  transition: transform 1s ease;
}#bttn-arrow:hover, #bttn-power:hover {
  transform: rotate(360deg);
}
